import React from "react";
import * as Yup from "yup";
import { Formik } from "formik";
import { Link } from "react-router-dom";

// Components
import Header from "../../components/Header";
import IconBack from "../../assets/images/icon-arrow-back.png";

import {
  MainContainer,
  Container,
  Row,
  Wallpaper,
  Form,
  Title,
  Description,
  Input,
  Footer
} from "./styles";

export default function Signup() {
  return (
    <>
      <Header title="Registrar-se" />
      <MainContainer className="mt-5 mb-5">
        <Container>
          <Row>
            <Wallpaper md />
            <Formik
              initialValues={{
                username: ""
              }}
              onSubmit={async values => {
                await new Promise(resolve => setTimeout(resolve, 500));
                alert(JSON.stringify(values, null, 2));
              }}
              validationSchema={Yup.object().shape({
                username: Yup.string()
                  .email()
                  .required("Required"),
                password: Yup.string().required("Required"),
                confirmPassword: Yup.string().oneOf(
                  [Yup.ref("password"), null],
                  "Passwords must match"
                )
              })}
            >
              {props => {
                const {
                  values,
                  touched,
                  errors,
                  dirty,
                  isSubmitting,
                  handleChange,
                  handleBlur,
                  handleSubmit,
                  handleReset
                } = props;
                return (
                  <Form onSubmit={handleSubmit} className="col p-5">
                    <Title>
                      <Link to="/login">
                        <img src={IconBack} className="mr-4" alt="voltar" />
                      </Link>
                      Cadastrar
                    </Title>
                    <Description>Preencha os dados abaixo.</Description>
                    <Input
                      name="username"
                      value={values.username}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      className={
                        (errors.username && touched.username
                          ? "text-input border-danger"
                          : "text-input border-light") +
                        " form-control rounded-0 mt-4 border"
                      }
                      placeholder="E-mail"
                    />
                    <Input
                      name="password"
                      type="password"
                      autocomplete="new-password"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      className="form-control border-0 rounded-0 mt-4"
                      placeholder="Repetir Senha"
                    />
                    <Input
                      name="confirmPassword"
                      type="password"
                      autocomplete="new-password"
                      value={values.confirmPassword}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      className={
                        (errors.confirmPassword && touched.confirmPassword
                          ? "text-input border-danger"
                          : "text-input border-light") +
                        " form-control rounded-0 mt-4 border"
                      }
                      placeholder="Senha"
                    />
                    <div className="custom-control custom-checkbox my-1 mr-sm-2">
                      <input
                        type="checkbox"
                        className="custom-control-input"
                        id="continueConectado"
                      />
                      <label
                        htmlFor="continueConectado"
                        className="custom-control-label"
                      >
                        Aceito os Termos e Condies
                      </label>
                    </div>
                    <Footer className="mt-4 mb-4 w-100">
                      <button className="btn bg-primary text-white font-weight-bold pr-5 pl-5 pt-3 pb-3 w-100">
                        Cadastrar
                      </button>
                    </Footer>
                  </Form>
                );
              }}
            </Formik>
          </Row>
        </Container>
      </MainContainer>
    </>
  );
}
