import React from "react";
import * as Yup from "yup";
import { Formik } from "formik";
import { Link } from "react-router-dom";

// Components
import Header from "../../components/Header";

import {
  MainContainer,
  Container,
  Row,
  Wallpaper,
  Form,
  Title,
  Description,
  Input
} from "./styles";

export default function Login() {
  const handleSubmitLogin = async values => {
    await new Promise(resolve => setTimeout(resolve, 500)); // atrasar alert
    alert(JSON.stringify(values, null, 2));
  };

  return (
    <>
      <Header title="Login" />
      <MainContainer className="mt-5 mb-5">
        <Container>
          <Row>
            <Wallpaper md />
            <Formik
              initialValues={{ username: "" }}
              onSubmit={handleSubmitLogin}
              validationSchema={Yup.object().shape({
                username: Yup.string()
                  .email()
                  .required("Required")
              })}
            >
              {props => {
                const {
                  values,
                  touched,
                  errors,
                  dirty,
                  isSubmitting,
                  handleChange,
                  handleBlur,
                  handleSubmit,
                  handleReset
                } = props;
                return (
                  <Form onSubmit={handleSubmit} className="col-md p-4">
                    <Title>Entrar</Title>
                    <Description>
                      Seja um dos nossos. <Link to="/signup">Faa parte!</Link>
                    </Description>
                    <Input
                      name="username"
                      value={values.username}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      className={
                        (errors.username && touched.username
                          ? "text-input border-danger"
                          : "text-input border-light") +
                        " form-control rounded-0 mt-4 border"
                      }
                      placeholder="E-mail"
                    />
                    <Input
                      name="password"
                      type="password"
                      value={values.password}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      className="form-control border-0 rounded-0 mt-4"
                      placeholder="Senha"
                    />
                    <div className="custom-control custom-checkbox my-1 mr-sm-2">
                      <input
                        type="checkbox"
                        className="custom-control-input"
                        id="continueConectado"
                      />
                      <label
                        htmlFor="continueConectado"
                        className="custom-control-label"
                      >
                        Continuar conectado
                      </label>
                    </div>
                    <div className="mt-4 mb-4">
                      <button
                        type="submit"
                        className="btn bg-primary text-white font-weight-bold pr-5 pl-5 pt-3 pb-3 rounded-0"
                      >
                        <span className="pr-5 pl-5">Entrar</span>
                      </button>
                    </div>
                    <Description className="m-0">
                      <p>
                        <Link to="/forgot">Esqueceu sua senha?</Link>
                      </p>
                      <p>
                        <Link to="/signup">Ainda no tem sua Conta?</Link>
                      </p>
                    </Description>
                  </Form>
                );
              }}
            </Formik>
          </Row>
        </Container>
      </MainContainer>
    </>
  );
}
