import React, { useState } from "react";
import { NavLink, useHistory, Link } from "react-router-dom";

import { Nav, List, Page, Actions, ToggleMenu } from "./styles";

import { ReactComponent as IconHamburgerButton } from "../../../assets/images/icon-hamburger-button.svg";
import { ReactComponent as IconCloseButton } from "../../../assets/images/icon-close-button.svg";

export default function Menu() {
  let history = useHistory();

  const [toggleMenu, setToggleMenu] = useState(false);

  const handlerSendTo = page => {
    history.push(page);
  };

  return (
    <>
      <div className={`w-100 d-flex ${toggleMenu ? "show" : "hide"}`} id="menu">
        <Nav>
          <List>
            <Page>
              <NavLink to="/" exact activeClassName="active">
                Incio
              </NavLink>
            </Page>
            <Page>
              <NavLink to="/invite-affiliate" activeClassName="active">
                Convide e Ganhe
              </NavLink>
            </Page>
            <Page>
              <NavLink to="/about" activeClassName="active">
                Sobre Ns
              </NavLink>
            </Page>
            <Page>
              <NavLink to="/corporation" activeClassName="active">
                Empresas
              </NavLink>
            </Page>
            <Page>
              <NavLink to="/blog" activeClassName="active">
                Blog
              </NavLink>
            </Page>
            <Page>
              <NavLink to="/contact-us" activeClassName="active">
                Contato
              </NavLink>
            </Page>
          </List>
        </Nav>
        <Actions>

            <button 
              className="btn bg-transparent text-white font-weight-bold pr-5 pl-5 "
            >
              <Link to="/signup">
              Cadastrar
              </Link>
            </button>

    
      
          <button
            onClick={() => handlerSendTo("/login")}
            className="btn bg-primary text-white font-weight-bold pr-5 pl-5"
          >
            Entrar
          </button>
        </Actions>
      </div>
      <ToggleMenu
        onClick={() => {
          if (!toggleMenu) {
            setToggleMenu(true);
          }
        }}
      >
        <div onClick={() => setToggleMenu(false)}>
          <IconCloseButton style={{width:22}} className={`${toggleMenu ? "show" : "hide"}`} />
        </div>
        <IconHamburgerButton />
      </ToggleMenu>
    </>
  );
}
