import React from "react";
import { BrowserRouter, Switch, Route } from "react-router-dom";

// Middlewares
import RouteAuth from "../middlewares/RouteAuth";

import Home from "../pages/Home";
import Login from "../pages/Login";
import Signup from "../pages/Signup";

import Passo1 from "../pages/Passo1";
import Passo2 from "../pages/Passo2";
import Pass3 from "../pages/passo3";
import Pass4 from "../pages/passo4";

// Dashboard
import HomeDashboard from "../pages/Dashboard/Home";

export default function Routes() {
  return (
    <BrowserRouter>
      <Switch>
        <Route path="/" exact component={Home} />
        <Route path="/login" component={Login} />
        <Route path="/signup" component={Signup} />
        <Route path="/passo1" component={Passo1} />
        <Route path="/passo2" component={Passo2} />
        <Route path="/passo3" component={Pass3} />
        <Route path="/passo4" component={Pass4} />

        <RouteAuth path="/dashboard">
          <Route path="/dashboard" exact component={HomeDashboard} />
        </RouteAuth>
      </Switch>
    </BrowserRouter>
  );
}
