import styled from 'styled-components';
import { Container as BootstrapContainer } from "react-bootstrap";

import Passo2 from "../../assets/images/passo2.png";
import Passo2Mob from "../../assets/images/passo2-mob.png";


const Container = styled(BootstrapContainer)`
    background-color:#FFF;
    margin-top: 100px;
    flex:1
    width: 100%;
    height: 100%;
    @media (max-width: 411px) {
      { 
        
        width: 500px;
        heigth: 500px;
        background-color:#FFF;
        margin-top: 100px;
        margin-left: 1px !important;
      }
    },

    
    @media (max-width: 991.98px) {
      { 
        flex:1;
        background-color:#FFF;
        margin-top: 100px;
        margin-left: 1px !important;
      }
    },

    @media (max-width: 320px) {
      { 
        width: 900px;
        heigth: 500px;
        background-color:#FFF;
        margin-top: 100px;
        margin-left: 1px !important;
      }
    },
`;

const ImageFundPasstwo = styled.div`
  background: url("${Passo2}") no-repeat;
  width: 100%;
  height: 100%;
  position: absolute;
  margin-top: 100px;
  margin-left: 100px;

  @media (max-width: 991.98px) {
    {
      flex:1;
      background: url("${Passo2Mob}") no-repeat;
      align-items: center;
      justify-content: center;
      background-size: 250px;
      
    }
  }
  

`;
const TitleHeader = styled.h1`
  color: #007BFF;
  font-size: 24px;
  width: 172px;
  height: 32px;
  position: absolute;
  margin: 0;
  margin-top: 60px;
  margin-left: 85px;
  margin-bottom: 12px;
  
  @media (max-width: 991.98px) {
    {
      align-items: center;
      margin-left: 110px;
      justify-content:center;
      text-align:center;
      
    }
  }
`;
const Title = styled.h2`
  color: #000;
  width: 450px;
  height: auto;
  margin: auto;
  margin-top: 180px;
  font-size: 20px;
  margin-left: 420px;
  position: absolute;
  @media (max-width: 991.98px) {
    {
      margin-left: 66px;
      margin-top: 160px;
      white-space: nowrap;
    }
  }
 
`;

const Grid = styled.div`
  background: linear-gradient(90deg, rgba(0,123,255,25) 0%, rgba(0,123,255,1) 0%, rgba(0,123,255,0.14049369747899154) 0%);
  max-width: 700px;
  padding: 12px;
  margin-top: 250px;
  margin-left: 400px;
  position: absolute;
  display: auto;
  @media (max-width: 991.98px) {
    {
      margin-left: 77px;
      margin-top: 280px;
    }
  }
  
`;

const Thead = styled.thead`
  
`;

const Adicionar = styled.button`
    background-color: #007bff;
    color: #FFF;
    transition: all 0.5s ease;
    text-align: center;
    margin: 50px auto;
    margin-top: 190px;
    margin-left: 800px;
    position: absolute;
    user-select: none;
    border-radius: 0.25rem;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.15);
    border: 1px solid transparent;
    padding: 0.5rem 2rem;
    line-height: 1.5;
    font-size: ${({ fontSize }) => (fontSize ? fontSize : "1rem")};
    &:hover {
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.3);
    }

    @media (max-width: 991.98px) {
      {
        margin: 50px auto;
        margin-left: 55px;
        margin-top: 200px;
      }
    }
`;

const Continuar = styled.button`
    background-color: #007bff;
    color: #FFF;
    transition: all 0.5s ease;
    text-align: center;
    justify-content: center;
    margin: 50px auto;
    margin-top: 550px;
    margin-left: 450px;
    position: absolute;
    user-select: none;
    border-radius: 0.25rem;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.15);
    border: 1px solid transparent;
    padding: 0.5rem 2rem;
    line-height: 1.5;
    font-size: ${({ fontSize }) => (fontSize ? fontSize : "1rem")};
    &:hover {
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.3);
    }
    @media (max-width: 991.98px) {
      {
        margin: 50px auto;
        margin-left: 150px;
        margin-top: 420px;
      }
    }
`;

const Voltar = styled.button`
    background-color:   transparent;
    color: #007bff;
    widht: 21px;
    text-align: center;
    justify-content: center;
    margin: 50px auto;
    margin-top: 600px;
    margin-left: 485px;
    position: absolute;  
    border:   transparent;
    font-size: ${({ fontSize }) => (fontSize ? fontSize : "1rem")};
    @media (max-width: 991.98px) {
      {
        margin: 50px auto;
        margin-left: 190px;
        margin-top: 477px;
      }
    }
`;


export { 
    Container,
    ImageFundPasstwo,
    TitleHeader,
    Title,
    Grid,
    Thead,
    Adicionar,
    Continuar,
    Voltar
};