import styled from 'styled-components';
import { Container as BootstrapContainer} from "react-bootstrap";

import { Col } from "react-bootstrap";

import Passo1 from "../../assets/images/passo1.png";
import Passo1mob from "../../assets/images/passo1-mobi.png";



const Container = styled(BootstrapContainer)`
    background-color:#FFF;
    margin-top: 100px;
    flex:1
    width: 100%;
    height: 80%;

    @media (max-width: 991.98px) {
      { 
        flex:1;
        background-color:#FFF;
        margin-top: 100px;
        margin-left: 7px;
      }
    }
`;


const ImageFundPassOne = styled.div`
  background: url("${Passo1}") no-repeat;
  width: 100%;
  height: 100%;
  position: absolute;
  margin-top: 100px;
  margin-left: 100px;

  @media (max-width: 991.98px) {
    {
      flex:1;
      background: url("${Passo1mob}") no-repeat;
      margin-left: 45px;
      margin-top: 90px;
      background-size: 250px;
      
    }
  }
  

`;
const TitleHeader = styled.h1`
  color: #007BFF;
  font-size: 24px;
  width: 172px;
  height: 32px;
  position: absolute;
  margin: 0;
  margin-top: 60px;
  margin-left: 85px;
  margin-bottom: 12px;
  @media (max-width: 991.98px) {
    {
      margin-left: 75px auto;
      margin-top: 15px auto;
    }
  }
`;


const Title = styled.div`
  font-size: 32px;

  strong {
    color: #007bff;
    font-size: 50px;
    font-weight: 900;
  }

  @media (max-width: 991.98px) {
    color: #fff;
    margin-top: 1.5em auto;
    text-align: center auto;
   
  }
  h3 {
    color: #FFF;
    font-style: bold;
  }
`;


const Description = styled.div`
  font-size: 18px;
  margin: 24px 0 48px;
`;


const Actions = styled.div`
  .btn {
    font-size: 16px;
  }
  .btn.border {
    border-width: 2px !important;
  }

  @media (max-width: 991.98px) {
    display: flex;
    flex-direction: column;

    .btn {
      margin: 0 0 0.7rem 0 !important;
    }
  }
`;

const Form = styled.form`
  
  background-color: #fff;
  color: #007bff;
  font-weight: bold;
  margin: 50px auto;
  margin-top: 250px;
  margin-left: 320px;
  position: absolute;
  @media (max-width: 991.98px) {
    {
      top: 120px;
      margin: 50px auto;
      margin-top: 120px;
    }
  }
`;

const GroupInput = styled.div`
  margin-bottom: 20px;
`;

const Label = styled.div`
  margin-bottom: 16px;
`;

const InputContainer = styled.div`
  border: rgba(0, 123, 255, 0.32) 2px solid;
  border-radius: 4px !important;
  input {
    border-radius: 4px !important;
    color: #000;
    font-weight: bold;
    height: 50px;
  }
  input::placeholder {
    color: rgba(0, 0, 0, 0.24);
  }
 
`;

const Taxas = styled.div`
  color: #000;
`;


export { 
    Container,
    TitleHeader,
    ImageFundPassOne,
    Col,
    Title,
    Description,
    Actions,
    Form,
    GroupInput,
    Label,
    InputContainer,
    Taxas
};