import styled from "styled-components";

import Colors from "../../../../Constants/Colors";

const Container = styled.div`
  @media (max-width: 991.98px) {
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 2;
    top: 0;
    right: 0;

    &.show {
      display: flex !important;
      opacity: 1;
    }

    &.hide {
      display: none !important;
      opacity: 0;
    }

    &::before {
      content: "";
      background: #000;
      width: 100%;
      height: 100%;
      position: absolute;
      left: -100%;
      opacity: 0.87;
      z-index: 1;
    }
  }
`;

