import React, { useState, useEffect } from "react";
import { NavLink } from "react-router-dom";


import { Formik, ErrorMessage, useFormik } from "formik";

import { CurrencySelect } from "../../CurrencySelect";

import axios from "axios";

import {
  MainContainer,
  Container,
  Title,
  Description,
  Actions,
  Form,
  GroupInput,
  Label,
  InputContainer,
  Taxas
} from "./styles";

export default function Spotlight({setStep}) {

  const formik = useFormik({
    initialValues: {
      email: "",
      send: "",
      recipient: 0,
      sendCurrency: {
        value: "brl",
        label: "BRL"
      },
      recipientCurrency: {
        value: "eur",
        label: "EUR"
      }
    },
    onSubmit:  async  values => {
      try {
        console.log('Values aqui !', values)
      }
      catch(error){
          console.log('error' , error)
      }
    }
  });

  // State para mudanca
  const [coins, setCoins] = useState([]);


  // Function para conversao 
  useEffect(() => {
    async function getCoins() {
      await axios
        .get("https://api.exchangeratesapi.io/latest")
        .then(response => {
          const keys = Object.keys(response.data.rates);

          const options = keys.map(key => {
            return {
              value: key.toLowerCase(),
              label: key.toUpperCase()
            };
          });

          setCoins(options);
        });
    }

    getCoins();
  }, []);



  async function convert() {
    await axios
      .get(
        `https://api.exchangeratesapi.io/latest?base=${formik.values.sendCurrency.label}`
      )
      .then(response => {
        const baseValue =
          response.data.rates[formik.values.recipientCurrency.label];

        const send = formik.values.send;

        formik.setValues({ ...formik.values, recipient: baseValue * send });
      });
  }
 
  useEffect(() => {
    if (formik.values.sendCurrency) {
      convert("");
    }
  }, [
    formik.values.send,
    formik.values.sendCurrency.value,
    formik.values.recipientCurrency.value
  ]);







  return (
    <MainContainer className="w-100">
      <Container>
       
            <Title>
              <h3>Diga adeus a</h3>
              <div>
                <strong>Taxas Altas</strong>
              </div>
            </Title>
            <Description>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
              eiusmod tempor incididunt ut labore et dolore magna aliqua.
              Bibendum est ultricies integer quis. Iaculis urna id volutpat
              lacus
            </Description>
            <Actions>
              <NavLink
                to="/signup"
                className="btn bg-primary text-white font-weight-bold pr-5 pl-5 pt-2 pb-2 mr-3"
              >
                Criar Conta
              </NavLink>
              <NavLink
                to="/login"
                className="btn bg-white text-primary border border-primary font-weight-bold pr-5 pl-5 pt-2 pb-2"
              >
                Entrar
              </NavLink>
            </Actions>
          
       
          <Formik
            initialValues={formik.values}

            onSubmit={(values, { setSubmitting }) => {
              setStep({ step: "confirm", details: { ...values } });
              setSubmitting(false);
            }}
          >
            <Form className="send-money p-4" method="POST">
              <GroupInput>
                <Label>Voc envia:</Label>
                <InputContainer 
                className="input-group mb-3"
                
                >

                  <input
                    placeholder="0"
                    className="form-control border-0 bg-white"
                    id="send"
                    type="number"
                   
                    min="10"
                    name="send"
                    onChange={formik.handleChange}
                    value={formik.values.send}
                  />
                  <div className="input-group-append">
                    <CurrencySelect
                      options={coins}
                      defaultValue={formik.values.sendCurrency}
                      onChange={formik.setFieldValue}
                      value={formik.values.sendCurrency}
                      name="sendCurrency"
                      isSearchable
                    />
                  </div>
                </InputContainer>
              </GroupInput>
              <GroupInput>
                <Label>O destinatrio recebe:</Label>
                <InputContainer className="input-group mb-3">
                  <input
                    placeholder="0"
                    className="form-control border-0 bg-white"
                    id="recipient"
                    type="number"
                    name="recipient"
                    value={formik.values.recipient.toFixed(2)}
                    readOnly
                  />
                  <div className="input-group-append">
                    <CurrencySelect
                      options={coins}
                      name="recipientCurrency"
                      defaultValue={formik.values.recipientCurrency}
                      value={formik.values.recipientCurrency}
                      onChange={formik.setFieldValue}
                      isSearchable
                    />
                  </div>
                </InputContainer>
              </GroupInput>
              <Taxas className="mt-2 mb-4">
                <small>Taxa: 1,2% * total</small>
              </Taxas>
              <button
                className="btn bg-primary text-white font-weight-bold w-100 pt-3 pb-3"
                type="submit"
              >
                Enviar dinheiro
              </button>
            </Form>
            </Formik>
        
      </Container>
    </MainContainer>
  );
}
