import React from "react";

import { Link } from "react-router-dom"


import {
    Container,
    Title,
    NumeroOne,
    Wrapper,
    Information,
    TitleInformation,
    TransactionTitle,
    DivisionY,
    Beneficiario,
    BeneficiarioTitle,
    Button,
    Voltar

    
  } from "./styles";

export default function passo3(){

    return (
        <>
       
        <Container>
            <Title>
                Enviar Dinheiro
            </Title>
            <NumeroOne />
            <Wrapper>
                <TitleInformation>Verifique as informaes</TitleInformation>
                <Information>
                    <TransactionTitle>Transao</TransactionTitle>
                    <p >Qtd. para Enviar</p>
                    <p >Qtd. para Receber</p>
                    <p >Tipo de Cmbio</p>
                    <p >Custo Total</p>
                    <p >Data de Incio</p>
                    <p >Data de Trmino</p>
                </Information>
                <DivisionY />
                <Beneficiario>
                    <BeneficiarioTitle>Beneficirio</BeneficiarioTitle>
                    <p >Nome: </p>
                    <p >Pas:</p>
                    <p >Banco:</p>
                    <p >N da Conta:</p>
                    <p >Tipo de Routing(?):</p>
                    <p >N de Routing(?):</p>
                </Beneficiario>
            </Wrapper>
            <Button>Continuar</Button>
            <Link to="/passo2">
                <Voltar>Voltar</Voltar>
            </Link>
        </Container>
        </>
        
    )
}