import React from "react";
import { Route, Redirect } from "react-router-dom";

export default function Middleware({ children, ...rest }) {
  const isAuthenticated = true; // if logged in set true, don't isn't logged, set to false

  return (
    <Route
      {...rest}
      render={({ location }) =>
        isAuthenticated ? (
          children
        ) : (
          <Redirect
            to={{
              pathname: "/login",
              state: { from: location }
            }}
          />
        )
      }
    />
  );
}
