import React, { useState } from "react";
import Dropdown from "react-bootstrap/Dropdown";

import { NavLink, useHistory } from "react-router-dom";

import {
  HeaderMain,
  HeaderContainer,
  SizeLogo,
  Logo,
  ToggleMenu
} from "../../../components/Header/styles";
import { Actions } from "./styles";
import { ReactComponent as IconHamburgerButton } from "../../../assets/images/icon-hamburger-button.svg";

import Menu from "./Menu";

export default function HeaderDashboard() {
  const [toggleMenu, setToggleMenu] = useState(false);

  let history = useHistory();

  const handlerSendTo = page => {
    history.push(page);
  };

  return (
    <>
      <HeaderMain className="dashboard">
        <HeaderContainer>
          <SizeLogo>
            <Logo to="/">Cambio Online</Logo>
          </SizeLogo>
          <Actions>
            <button
              onClick={() => handlerSendTo("/login")}
              className="btn bg-primary text-white font-weight-bold pr-5 pl-5 mr-5"
            >
              Enviar Dinheiro
            </button>
            <ToggleMenu
              onClick={() => {
                if (!toggleMenu) {
                  setToggleMenu(true);
                }
              }}
            >
              <IconHamburgerButton />
            </ToggleMenu>
            <Dropdown alignRight id="dropdown-item-button">
              <Dropdown.Toggle
                className="bg-transparent shadow-none text-dark border-0 rounded-0"
                variant="dark"
              >
                <img
                  src="https://pbs.twimg.com/profile_images/1057247951150440448/pGgJeSoC_200x200.jpg"
                  className="rounded-circle avatar"
                  alt="Lenivene"
                />
                Lenivene Bezerra
              </Dropdown.Toggle>

              <Dropdown.Menu>
                <Dropdown.Item>Alguma coisa</Dropdown.Item>
                <Dropdown.Item>Outra coisa</Dropdown.Item>
                <Dropdown.Item>Something else</Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
          </Actions>
        </HeaderContainer>
      </HeaderMain>
      <Menu toggleMenu={toggleMenu} setToggleMenu={setToggleMenu} />
    </>
  );
}
