import styled from 'styled-components';
import { Container as BootstrapContainer } from "react-bootstrap";

import Passo3 from "../../assets/images/passo3.png";
import DivisonTop from "../../assets/images/Rectangle-955.png";

const Container = styled(BootstrapContainer)`
    background-color:#FFF;
    margin-top: 100px;
    width: 100%;
    height: 80%;
    justify-content: center;
`;

const Wrapper = styled.div`
    width: 100%;
    height: 100%;
    display: block;
    justify-content: wrap;
    display: flex;
`;

const Information = styled.div`
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  margin: 50px auto;
  margin-top: 100px;
  margin-left: 150px;
`;

const TitleInformation = styled.h1`
  font-size: 18px;
  text-align: center;
  justify-content: center;
  margin 50px auto;
  margin-top: -12px;
  margin-left: 450px;
  white-space: nowrap;
`;



const WrapperInput = styled.div`

    width: 320px;
    margin-left: 490px;
    display: incline;
`;

const TransactionTitle = styled.h2`
   font-size: 16px;
   color:#007BFF;
   width: 84px;
   height: 24px;
   margin: 50px auto;
   margin-top: -50px;
   margin-left: 150px;
   position: absolute;
   
`;

const Beneficiario = styled.div`
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  margin: 50px auto;
  margin-top: 100px;
  margin-left: 600px;

`;

const BeneficiarioTitle = styled.h2`
  font-size: 16px;
  color:#007BFF;
  width: 84px;
  height: 24px;
  margin: 50px auto;
  margin-top: -50px;
  margin-left: 150px;
  position: absolute;

`




const Title = styled.p`
    color: #007BFF;
    font-style:bold;
    font-size: 18px;
    margin: 36px;
    display: -webkit-inline-box;
`;


const Button = styled.button`
    background-color: #007bff;
    color: #FFF;
    transition: all 0.5s ease;
    vertical-align: middle;
    text-align: center;
    display: block;
    user-select: none;
    border-radius: 0.25rem;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.15);
    border: 1px solid transparent;
    padding: 0.5rem 2rem;
    margin-top: -300px;
    margin-left: 420px;
    align-items: center;
    justify-content: center;
    line-height: 1.5;
    font-size: ${({ fontSize }) => (fontSize ? fontSize : "1rem")};

    &:hover {
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.3);
    }
`;

const Voltar = styled.a`
  color: #007bff;
  display: block;
  margin: 50px auto;
  position: absolute;
  margin-left: 460px;
  margin-top: 20px;

`

const Label = styled.div`
  margin-bottom: 16px;
`;

const NumeroOne = styled.image`
 @media (min-width: 700px) {
        background: url("${Passo3}") no-repeat;
        widht: 80px;
        height: 80px;
        margin-top: 1%;
        margin-left: 100px;
        display: flex;
        background-size: 940px;
    }
    
    @media (max-width: 1400px) {
        background: url("${Passo3}") no-repeat;
        widht: 80px;
        height: 80px;
        margin-top: 1%;
        margin-left: 120px;
        display: flex;

    }   
`;

const GroupInput = styled.div`
  margin-bottom: 20px;
`;

const InputContainer = styled.div`

  
  input {
    border-radius: 4px !important;
    color: #000000;;
    font-weight: bold;
    border: none;
    height: 40px;
    widht: 120px;
    
  }
  input::placeholder {
    color: rgba(0, 0, 0, 0.24);
  }
 
`;

const DivisionY = styled.div`
  background: url("${DivisonTop}") no-repeat;
  width: 2px;
  height: 242px;
  margin: 50px auto;
  margin-top: 100px;
  margin-left: 500px;
  position: absolute;
  
`


export { 
    Container,
    Title,
    NumeroOne,
    Button,
    Wrapper,
    Label,
    GroupInput,
    InputContainer,
    WrapperInput,
    DivisionY,
    Information,
    TitleInformation,
    TransactionTitle,
    Beneficiario,
    BeneficiarioTitle,
    Voltar
    
};