import styled from 'styled-components';
import { Container as BootstrapContainer } from "react-bootstrap";

import Passo4 from "../../assets/images/passo4.png";
import Banking from "../../assets/images/Group-1810.png";
import Deposito from "../../assets/images/Group-1819.png";

const Container = styled(BootstrapContainer)`
    background-color:#FFF;
    margin-top: 100px;
    width: 100%;
    height: 80%;
    justify-content: center;
`;

const SmallCusto = styled.small`
    font-size: 12px;
    color:#000;
    text-align: center;
    justify-content: center;
    margin: 50px auto;
    position: absolute;
    margin-left: 290px;
    margin-top: -30px;

`;

const TitleBankingContainer = styled.h2`
    font-size: 15px;
    color:#007BFF;
    text-align: center;
    justify-content: center;
    margin: 50px auto;
    position: absolute;
    margin-left: 250px;
    margin-top: -50px;

`;

const TitlePagment = styled.h1`
    font-size: 24px;
    text-align: center; 

`;

const Wrapper = styled.div`
    width: 100%;
    height: 100%;
    display: block;
    justify-content: wrap;
    display: flex;
    p{
        text-align: center;
        justify-content: center;
        display: block;
        position: absolute;
        margin-left: 200px;
        margin-top: 350px;
    }
`;

const Title = styled.p`
    color: #007BFF;
    font-style:bold;
    font-size: 18px;
    margin: 36px;
    display: -webkit-inline-box;
`;

const BankingGround = styled.div`
    background: url("${Banking}") no-repeat;
    width: 66px;
    height: 66px;


`;

const BankingDeposito = styled.div`
    background: url("${Deposito}") no-repeat;
    width: 66px;
    height: 66px;


`;

const NumeroOne = styled.div`
 @media (min-width: 700px) {
        background: url("${Passo4}") no-repeat;
        widht: 80px;
        height: 80px;
        margin-top: 1%;
        margin-left: 100px;
        display: flex;
        background-size: 940px;
    }
    
    @media (max-width: 1400px) {
        background: url("${Passo4}") no-repeat;
        widht: 80px;
        height: 80px;
        margin-top: 1%;
        margin-left: 120px;
        display: flex;

    }   
`;

const Grid = styled.div`
  background: linear-gradient(90deg, rgba(0,123,255,25) 0%, rgba(0,123,255,1) 0%, rgba(0,123,255,0.14049369747899154) 0%);
  max-width: 700px;
  width: 702px;
  height: 101px;
  padding: 12px;
  margin: 50px auto;
  margin-top: 70px;
  margin-left: 172px;
  position: absolute;
  display: auto;
`;

const GridDeposito = styled.div`
    background: #fff;
    max-width: 700px;
    width: 702px;
    height: 101px;
    padding: 12px;
    margin: 50px auto;
    margin-top: 190px;
    margin-left: 172px;
    position: absolute;
    display: auto;
    border-radius: 2px;
    border: 1px solid  #007BFF;

`;

const Button = styled.button`
    background-color: #007bff;
    color: #FFF;
    transition: all 0.5s ease;
    vertical-align: middle;
    text-align: center;
    user-select: none;
    border-radius: 0.25rem;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.15);
    border: 1px solid transparent;
    padding: 0.5rem 2rem;
    line-height: 1.5;
    font-size: ${({ fontSize }) => (fontSize ? fontSize : "1rem")};
    &:hover {
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.3);
    }
    margin: 50px auto;
    margin-left: 450px;
    margin-top: -350px;
    position: absolute;

    @media (min-width: 1400px) {
        margin: 50px auto;
        margin-left: 450px;
        margin-top: -300px;
        position: absolute;
    }  
`;


export { 
    Container,
    Wrapper,
    Title,
    NumeroOne,
    Grid,
    BankingGround,
    TitlePagment,
    TitleBankingContainer,
    SmallCusto,
    GridDeposito,
    BankingDeposito,
    Button
};